import uniStatusBar from '../../components/uni-status-bar/uni-status-bar.vue';
import bottomButtonBar from '../../components/common/bottom-button-bar.vue';
export default {
	data() {
		return {
			phone: '',
			code: '',
			account: '',
			password: '',
			byPhone: true,
			timing: 0,
			providerList: [],
			userinfo: {}
		};
	},
	methods: {
		getuserinfo(e){
			this.userinfo= e.detail.userInfo
		},
		loginwx() {
			

			wx.cloud.callFunction({
				// 云函数名称
				name: 'login',
				// 传给云函数的参数
				success: (res) => {
					// 将用户信息和 微信id 对象合并成一个对象
					this.userinfo = Object.assign(this.userinfo, res.result.event.userInfo)
					uni.setStorage({
					    key: 'user-info',
					    data: this.userinfo,
					    success: function () {
							uni.switchTab({
								url:'../home/home'
							})
					    }
					});
					
					
				},
				fail: console.error
			});

		},
		sentMsg() {
			//发送短信
			if (this.timing !== 0 || !this.checkCode) {
				return;
			}
			this.timing = 5;
			let timer = setInterval(() => {
				this.timing--;
				if (this.timing === 0) {
					this.timing = 0;
					clearInterval(timer);
				}
			}, 1000);
		},
		toggle() {
			this.byPhone = !this.byPhone;
		},
		check() {
			//验证手机号是否合法
			var mPattern = /^1[34578]\d{9}$/;
			if (!mPattern.test(this.phone)) {
				uni.showToast({
					title: '请填写正确的手机号',
					icon: 'none'
				});
			}
			return true;
		},
		submit() {
			//提交修改密码
			if (this.check()) {
				uni.showLoading({
					title: '登录中...'
				});
				//模拟
				setTimeout(() => {
					uni.hideLoading();
					uni.showToast({
						title: '登录成功',
						icon: 'none'
					});
				}, 2000);
			}
		},
		tologin(provider) {
			uni.login({
				provider: provider.id,



				success: res => {
					console.log('login success:', res);
					// 更新保存在 store 中的登录状态
					// this.login(provider.id);
				},
				fail: err => {
					console.log('login fail:', err);
				}
			});
		}
	},
	computed: {
		isDisabled() {
			// 按钮是否disabled的判断
			let { byPhone, phone, password, code, account } = this;
			return (!byPhone || !phone.length || !code.length) && (byPhone || !account.length || !password.length);
		},
		checkCode() {
			// 检查验证码是否为6位
			let reg = /^\d{6}$/;
			return reg.test(this.code);
		}
	},
	mounted() {
		uni.getProvider({
			service: 'share',
			success: result => {
				console.log(result);
				this.providerList = result.provider.map(value => {
					console.log(value);
					let providerName = '';
					let icon;
					switch (value) {
						case 'weixin':
							providerName = '微信登录';
							icon = 'icon-weixin';
							break;
						case 'qq':
							providerName = 'QQ登录';
							icon = 'icon-QQ';
							break;
						case 'sinaweibo':
							providerName = '新浪微博登录';
							icon = 'icon-xinlangweibo';
							break;
					}

					return {
						name: providerName,
						id: value,
						icon
					};
				});
			},
			fail: error => {
				console.log('获取登录通道失败', error);
			}
		});
		// uni.getStorage({
		//     key: 'userinfo',
		//     success: function (res) {
		// 		console.log(this.userinfo);
		//     }
		// });
	},
	components: {
		uniStatusBar,
		bottomButtonBar
	}
};
